.container {
    font-family: Arial, sans-serif;
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    background-color: #f9f9f9;
    border-radius: 8px;
    font-size: 18px; /* Increased base font size for the container */
}

.header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.header label {
    font-weight: bold;
    margin-right: 10px;
    font-size: 22px; /* Increased font size for header labels */
}

.header input {
    padding: 10px;
    font-size: 20px; /* Increased font size for input fields */
    border: 1px solid #ccc;
    border-radius: 4px;
}

.section {
    margin-bottom: 20px;
}

.section h2 {
    font-size: 28px; /* Increased font size for section headers */
    text-align: center;
    margin-bottom: 10px;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 10px;
    font-size: 18px; /* Increased font size for table content */
    table-layout: fixed; /* Ensure that columns have equal width */
}

table, th, td {
    border: 1px solid #ccc;
}

th, td {
    padding: 8px; /* Slightly increased padding for larger font sizes */
    text-align: center; /* Center align text for better readability */
    vertical-align: middle; /* Ensure content is vertically centered */
    box-sizing: border-box;
    overflow: hidden; /* Prevent content from overflowing */
    white-space: nowrap; /* Prevent text from wrapping to a new line */
    text-overflow: ellipsis; /* Add ellipsis if the text is too long */
}

select {
    width: 100%; /* Make sure the select box fits the cell */
    padding: 6px; /* Adjust padding to better fit within the cell */
    box-sizing: border-box; /* Ensure padding is included in the width */
    font-size: 16px; /* Adjusted font size to better fit within the select box */
}

input[type="time"] {
    width: 100%; /* Ensure the time input takes the full width of the cell */
    padding: 6px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box; /* Ensure padding is included in the width */
    height: 36px; /* Set a consistent height to accommodate time display */
}

button {
    padding: 12px 25px; /* Increased padding for buttons */
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px; /* Increased font size for buttons */
    display: block;
    margin: 20px auto 0;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #0056b3;
}
