/* MaintenanceForm.css */

.maintenance-form {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;
  }
  
  .form-group {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
  }
  
  .form-group label {
    flex: 0.2;
    margin-right: 10px;
  }
  
  .form-group input {
    flex: 0.8;
  }
  
  .report-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
    border: 1px solid #000;
    font-size: 14px;
  }
  
  .report-table th,
  .report-table td {
    border: 1px solid #000;
    padding: 5px;
    text-align: center;
    vertical-align: middle;
  }
  
  .report-table th {
    background-color: #f2f2f2;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 1.2em;
  }
  
  .report-table input[type="text"],
  .report-table input[type="time"],
  .report-table input[type="checkbox"],
  .report-table input[type="radio"] {
    width: 90%;
    padding: 5px;
    box-sizing: border-box;
  }
  
  .subheaders {
    display: flex;
    justify-content: space-around;
    margin-top: 5px;
  }
  
  .subheaders > div {
    flex: 1;
    font-weight: normal;
  }
  
  .signatures {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    flex-wrap: wrap;
  }
  
  .signature-group {
    flex: 1;
    margin-right: 10px;
    margin-bottom: 10px;
  }
  
  .signature-group label {
    display: block;
    margin-bottom: 5px;
  }
  
  .signature-group input {
    width: 100%;
    padding: 5px;
    box-sizing: border-box;
  }
  