/* src/EndLinePackageQuality.css */
.form-container {
  font-family: Arial, sans-serif;
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  border-radius: 8px;
}

.form-title {
  font-size: 26px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 8px;
  font-size: 20px;
}

.form-group input,
.form-group select,
textarea {
  width: 100%;
  padding: 12px;
  font-size: 18px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 30px;
  background-color: white;
}

.form-table th,
.form-table td {
  border: 1px solid #ccc;
  padding: 14px;
  font-size: 18px;
  text-align: left;
}

.form-table th {
  background-color: #f2f2f2;
  text-align: center;
}

textarea {
  width: 100%;
  padding: 12px;
  font-size: 18px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.comment-box {
  width: 100%;
  padding: 12px;
  font-size: 18px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Media queries for tablets */
@media (min-width: 768px) {
  .form-container {
    padding: 40px;
  }

  .form-title {
    font-size: 30px;
  }

  .form-group label {
    font-size: 24px;
  }

  .form-group input,
  .form-group select,
  textarea {
    font-size: 20px;
    padding: 14px;
  }

  .form-table th,
  .form-table td {
    font-size: 20px;
    padding: 16px;
  }

  textarea {
    font-size: 20px;
    padding: 14px;
  }
}
