  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  background-color: white;
}

.form-table th,
.form-table td {
  border: 1px solid #ccc;
  padding: 12px;
  font-size: 16px;
  text-align: left;
}

.form-table th {
  background-color: #f2f2f2;
  text-align: center;
}

textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Media queries for tablets */
@media (min-width: 768px) {
  .form-container {
    padding: 30px;
  }

  .form-title {
    font-size: 28px;
  }

  .form-group label {
    font-size: 20px;
  }

  .form-group input,
  .form-group select,
  textarea {
    font-size: 18px;
  }

  .form-table th,
  .form-table td {
    font-size: 18px;
  }

  .form-table td input,
  .form-table td select {
    font-size: 18px;
  }

  textarea {
    font-size: 18px;
  }
}
