.form-container {
    min-width: 1000px !important;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.form-title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: bold;
}

.form-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}

.form-group label {
    font-weight: bold;
    margin-bottom: 5px;
}

.form-row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 15px;
}

.form-row .form-group {
    flex: 1;
    min-width: 300px; /* Adjusted width to be more consistent with Jars & Lids form */
    margin-right: 20px;
}

.form-row .form-group:last-child {
    margin-right: 0;
}

.form-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
    table-layout: fixed; /* Ensure table cells have a consistent width */
}

.form-table th,
.form-table td {
    padding: 12px; /* Adjusted padding to be consistent with the Jars & Lids form */
    border: 1px solid #ccc;
    text-align: center;
    font-size: 16px; /* Increased font size for better readability */
    vertical-align: middle;
    box-sizing: border-box;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.form-table th {
    background-color: #f5f5f5;
    font-weight: bold;
    font-size: 18px; /* Increased font size to match header size */
}

.form-table input[type="time"],
.form-table select,
.form-table input[type="text"] {
    width: 100%; /* Ensure input fields take up the full width of the cell */
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

textarea {
    width: 100%;
    height: 120px;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: none;
}

button {
    padding: 12px 25px;
    margin: 15px 5px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #45a049;
}

button[type="button"] {
    background-color: #ff9800;
}

button[type="button"]:hover {
    background-color: #e68900;
}

button[type="submit"] {
    background-color: #2196F3;
}

button[type="submit"]:hover {
    background-color: #0b7dda;
}

.notes-container {
    margin-bottom: 20px;
}

.notes-container label {
    font-weight: bold;
}
